<template>
  <div class="add_advise_panel">
      <div class="title">写给作者</div>
      <div class="topic">
          <input type="text" v-model="title">
      </div>
      <div class="content">
          <textarea v-model="content"></textarea>
      </div>
      <div class="chose_star">
        <span style="color:darkred">留下你的评价吧</span>
          <el-rate
            v-model="value"
            show-text>
        </el-rate>
      </div>
      <button class="submit_data" @click="submitData">提交数据</button>
  </div>
</template>

<script>
export default {
    name : 'Advise',
    data(){
        return{
            value: null,
            title : '',
            content:''
        }
    },
    methods:{
        submitData(){
            if(this.$store.state.userData == ''){
                this.$bus.$emit('showAlert','请先登录')
            }
            else if(this.title == ''){
                this.$bus.$emit('showAlert','标题不能为空')
            }else if(this.content == '' ){
                this.$bus.$emit('showAlert','反馈内容不能为空')
            }else if(this.content.length < 10){
                this.$bus.$emit('showAlert','反馈内容不能过短')
            }else if(this.value == null){
                this.$bus.$emit('showAlert','你还没有进行评分')
            }else{
                let date = new Date()
                let time = `${date.getFullYear()} ${date.getMonth()+1}/${date.getDate()} ${date.getHours()}:${date.getMinutes()}`
                let info = 'user_id=' + this.$store.state.userData.id + '&'
                + 'title=' + this.title + '&'
                + 'content=' + this.content + '&'
                + 'star=' + this.value + '&'
                + 'time=' + time
                this.$http.post('/commitMyAdvise',info).then(
                    response=>{
                        if(response.data.code == 200){
                            this.$bus.$emit('showPrompt','数据提交成功')
                        }
                    },
                    error=>{
                        this.$bus.$emit('showAlert','数据提交失败')
                        console.log(error.message)
                    }
                )
            }
        }
    }
}
</script>

<style lang='less'>
@lightblack : rgb(61, 61, 61); // 亮黑
@green : rgb(28, 192, 6); // 绿色
@blue : rgb(16, 152, 206); // 蓝色
@import url("//unpkg.com/element-ui@2.15.6/lib/theme-chalk/index.css");
.add_advise_panel{
    width: 1200px;
    margin: 20px auto;
    padding: 0 0 50px 0;
    box-shadow: 0 0 10px @lightblack;
    .title{
        width: 1200px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        font-size: 30px;
        color: rgb(59, 59, 59);
    }
    .topic{
        position: relative;
        width: 1000px;
        height: 50px;
        margin: 50px 0 0 100px;
        background: grey;
        input{
            width: 100%;
            height: 100%;
            background: rgb(231, 231, 231);
        }
        input:focus{
            outline: 3px solid orange;
        }
    }
    .content{
        position: relative;
        width: 1000px;
        height: 200px;
        margin: 50px 0 0 100px;
        background: grey;
        textarea{
            width: 100%;
            height: 100%;
            resize: none;
            background: rgb(231, 231, 231);
        }
        textarea:focus{
            outline: 3px solid orange;
        }
    }
    .content:before,.topic:before{
        position: absolute; top: -40px; left: 0;
        width: 200px;
        height: 40px;
        line-height: 40px;
        content: '反馈';
        font-weight: bolder;
        color: rgb(194, 126, 0);
    }
    .topic:before{
        content:'标题'
    }
    .chose_star{
        width: 1000px;
        height: 100px;
        margin: 50px 0 0 100px;

    }
    .submit_data{
        width: 200px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        background: darkred;
        color: white;
        margin: 0 0 0 900px;
        transition: all .2s;
    }
    .submit_data:hover{
        background: rgb(231, 0, 0);
    }
}
</style>